
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import axios from 'axios'
export default {
components: { XHeader, Flexbox, FlexboxItem, Tab, TabItem, XProgress, XButton },
data() {
return {
tabIndex: 0,
percent:0,
collect:[],
years:new Date().getFullYear(),
proTotal:[],
projectList:{},
getList:[],
rate:[],
itegral:'',
results:''
};
},
methods:{
progress(){
axios.get('pscoreparty/getProjectScoreByUserId',{
params:{
userId:this.$store.getters.user.userId,
year:this.years
}
}).then( res => {
this.proTotal = res.data;
}).catch(err => {
console.log('fail'+err);
})
},
project(){
axios.get('pscoredetail/queryByRoleMenu',{
params:{
roleid:4,
}
}).then( res => {
let data = res.data;
let obj = {};
for(var i=0;i<data.length;i++){
obj[data[i].projectId] = data[i];
this.projectId = obj[data[i].projectId]
}
this.projectList  = obj;
}).catch(err => {
console.log('fail'+err);
})

},
getDetail(){
axios.get('pscoreparty/queryByUserId',{
params:{
userid:this.$store.getters.user.userid,
year:this.years
}
}).then( res => {
this.getList = res.data;
}).catch(err => {
console.log('fail'+err);
})

},
rating(){
let that = this;
that.score();
axios.get('pscorelevel/getList')
.then( res => {
this.rate = res.data;
for(let c in this.rate){
if(that.itegral >= this.rate[c].min && that.itegral <= this.rate[c].max){
this.results = this.rate[c].level
}
}
}).catch(err => {
console.log('fail'+err);
})

},
score(){
axios.get('pscoreparty/getSumScoreByUserId',{
params:{
userId:this.$store.getters.user.userid,
year:new Date().getFullYear()
}
})
.then( res => {
this.itegral = res.data;
}).catch(err => {
console.log('fail'+err);
})

},
dataPickers(dates){
let times = new Date(dates).toLocaleString()
Date.prototype.toLocaleString = function() {
return this.getFullYear() + "年" + (this.getMonth()+1) + "月" + this.getDate() + "日 "
};
return times;

}


},
mounted(){
this.progress();
this.project();
this.getDetail();
this.rating();


}
};



